import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useAuth } from '../AuthContext';
import '../styles/Dashboard.css';
import GetUserData from '../request/GetUserData';
import GetApplets from '../components/GetApplets';
import ExecuteApplet from '../components/ExecuteApplet';
import StopApplet from '../components/StopApplet';
import logo from '../assets/logo.png'; // Spécifiez le bon chemin vers votre logo

const Dashboard = () => {
  const history = useHistory();
  const [userData, setUserData] = useState(null);
  const { token } = useAuth();
  const [backgroundColor, setBackgroundColor] = useState('white');
  const [textColor, setTextColor] = useState('black');
  const [AppletsList, setAppletsList] = useState([]);

  const goSettings = () => {
    history.push('/Settings');
  };

  const goAction = () => {
    history.push('/Action');
  };

  const changeBackgroundColor = () => {
    const newBackgroundColor = backgroundColor === 'black' ? 'white' : 'black';
    setBackgroundColor(newBackgroundColor);
    localStorage.setItem('backgroundColor', newBackgroundColor);
    const newTextColor = textColor === 'white' ? 'black' : 'white';
    setTextColor(newTextColor);
    localStorage.setItem('textColor', newTextColor);

    // Mettez à jour la couleur de fond de la page
    document.body.style.backgroundColor = newBackgroundColor;
  };

  useEffect(() => {
    if (token && !userData) {
      GetUserData(token)
        .then((response) => {
          if (response.status === 200) {
            setUserData(response.data);
          } else {
            console.error("Erreur lors de la récupération des données de l'utilisateur.");
          }
        })
        .catch((error) => {
          console.error("Erreur lors de la récupération des données de l'utilisateur.", error);
        });
    }
    const savedBackgroundColor = localStorage.getItem('backgroundColor');
    const savedTextColor = localStorage.getItem('textColor');
    if (savedBackgroundColor) {
      setBackgroundColor(savedBackgroundColor);
      // Mettez à jour la couleur de fond du corps
      document.body.style.backgroundColor = savedBackgroundColor;
    }
    if (savedTextColor) {
      setTextColor(savedTextColor);
    }
  }, [token, userData]);

  useEffect(() => {
    GetApplets(token)
      .then((responseData) => {
        if (responseData.status === 200) {
          setAppletsList(responseData.data);
        } else {
          // console.error('Error fetching action list');
        }
      })
      .catch((error) => {
        console.error('Error fetching action list:', error);
      });
  }, [token]);

  function start(id) {
    ExecuteApplet(id, token).then((response) => {
      if (response.status === 200) {
        console.log('Service started: ', response);
      } else {
        console.log('Error while starting service');
      }
    });
  }

  function stop(id) {
    StopApplet(id, token).then((response) => {
      if (response.status === 200) {
        console.log('Service stopped: ', response);
      } else {
        console.log('Error while stopping service');
      }
    });
  }

  const squareButtonStyle = {
    backgroundColor: 'gray',
    borderColor: '#ffffff',
    color: 'white',
    width: '300px',
    height: '300px',
    fontSize: '80px',
  };

  return (
    <div>
      <img src={logo} alt="Your Logo" style={{ width: '300px', height: '300px' }} />
      <div className="dashboard-container" style={{ backgroundColor: backgroundColor }}>
        <div className="container-inner">
          <div className="left-side">
            <h1 style={{ color: textColor, fontFamily: 'Roboto, sans-serif' }}>
              Welcome {userData ? userData.firstname : ''}
            </h1>
            <div className="buttons-wrapper">
              <button onClick={goAction} className="btn btn-success button-spacing">
                Action
              </button>
              <button
                onClick={changeBackgroundColor}
                className="btn btn-light"
                style={{ backgroundColor: backgroundColor, borderColor: '#ffffff', color: textColor }}
              >
                Dark theme
              </button>
              <button onClick={goSettings} className="btn btn-light">
                Settings
              </button>
            </div>
          </div>
          <div className="right-side">
            {AppletsList.length === 0 ? (
              <p style={{ color: textColor, fontFamily: 'Roboto, sans-serif' }}>No applet yet</p>
            ) : (
              <div className="applets-container">
                {AppletsList.map((applet, index) => (
                  <div key={index} className="applet-rectangle">
                    <h2>{applet.name}</h2>
                    <p>{applet.description}</p>
                    <button className="start-button" onClick={() => start(applet._id)}>
                      Start
                    </button>
                    <button className="stop-button" onClick={() => stop(applet._id)}>
                      Stop
                    </button>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
