import React, { useState } from 'react';
import { useHistory, Link } from 'react-router-dom';
import InputText from '../components/InputText';
import InputPassword from '../components/InputPassword';
import logo from '../assets/logo.png';
import { useAuth } from '../AuthContext';
import '../styles/Home.css';
import { loginUser } from "../request/Login_request";
import axios from 'axios';

axios.defaults.baseURL = 'http://localhost:8080/';

function LoginForm() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const history = useHistory();
  const { login } = useAuth();
  const [errorMessage, setErrorMessage] = useState('');

  const getStoredTheme = () => {
    const savedBackgroundColor = localStorage.getItem('backgroundColor');
    const savedTextColor = localStorage.getItem('textColor');
    return {
      backgroundColor: savedBackgroundColor || 'white',
      textColor: savedTextColor || 'black',
    };
  };

  const [theme, setTheme] = useState(getStoredTheme);

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const handleTogglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const response = await loginUser(email, password);
      const { token } = response;
      login(token);
      history.push('/Dashboard');
    } catch (error) {
      setErrorMessage('Adresse e-mail ou mot de passe incorrect.');
    }
    setEmail('');
    setPassword('');
  };

  const GoogleLogin = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.get('/v1/auth/google');
      window.location.href = response.data.url;
    } catch (error) {
      console.error('Erreur lors de l\'authentification Google :', error);
    }
  };

  document.body.style.backgroundColor = theme.backgroundColor;
  document.body.style.color = theme.textColor;

  return (
    <div className="login-container">
      <img src={logo} alt="Logo" className="mb-3" width="100" height="100" />
      <h2>AreApp</h2>
      <form onSubmit={handleLogin}>
        <InputText
          id="email"
          label="Email"
          placeholder="Email"
          value={email}
          onChange={handleEmailChange}
        />
        <InputPassword
          id="password"
          label="Mot de passe"
          value={password}
          placeholder="Mot de passe"
          onChange={handlePasswordChange}
          showPassword={showPassword}
          onTogglePasswordVisibility={handleTogglePasswordVisibility}
        />
        <button type="submit" className="btn_submit">
          Se connecter
        </button>
        <button type="button" className="btn_submit_google" onClick={GoogleLogin}>
          Se connecter avec Google
        </button>
      </form>
      {errorMessage && <p style={{ marginTop: '40px' }} className="error-message">{errorMessage}</p>}
      <p className="text-center">
        Nouveau sur AreaApp ?{' '}
        <Link to="/RegistrationForm">S'enregistrer</Link>
      </p>
      <div className="line"></div>
    </div>
  );
}

export default LoginForm;
