import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useAuth } from '../AuthContext';
import '../styles/Settings.css';
import InputSettings from '../components/InputSettings';
import GetUserData from '../request/GetUserData';
import UpdateUserData from '../request/UpdateUserData';

const Settings = () => {
  const history = useHistory();
  const { token } = useAuth();
  const [userData, setUserData] = useState(null);

  const getStoredTheme = () => {
    const savedBackgroundColor = localStorage.getItem('backgroundColor');
    const savedTextColor = localStorage.getItem('textColor');
    return {
      backgroundColor: savedBackgroundColor || 'white',
      textColor: savedTextColor || 'black',
    };
  };

  const [theme, setTheme] = useState(getStoredTheme);

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await GetUserData(token);
        const userData = response.data;
        setUserData(userData);
      } catch (error) {
        console.error('Error fetching user data:', error);
      }
    };
    fetchUserData();
  }, [token]);

  const ChangeSettings = async (e) => {
    e.preventDefault();
    try {
      await UpdateUserData(userData, token);
    } catch (error) {
      console.error('Error updating user data:', error);
    }
  };

  const updateUserData = (field, value) => {
    setUserData((prevUserData) => ({
      ...prevUserData,
      [field]: value,
    }));
  };

  const goHome = () => {
    history.push('/Dashboard');
  };

  document.body.style.backgroundColor = theme.backgroundColor;
  document.body.style.color = theme.textColor;

  return (
    <div className="settings-container">
      <div className="left-side">
        <div className="welcome-text">
          <h1>Paramètres</h1>
          {/* Supprimez les lignes ci-dessous pour enlever les traits noirs et blancs au-dessus du bouton */}
          {/* <div className="line"></div> */}
          {/* <div className="line"></div> */}
          {/* Utilisez les classes Bootstrap pour styliser le bouton "Accueil" */}
          <button onClick={goHome} className="btn btn-lg btn-violet-fonce text-white">Accueil</button>
        </div>
        <div className="color-rectangle"></div>
      </div>
      <div className="right-side">
        <div style={{
          height: '25%',
        }}>
        </div>
        {userData ? (
          <form onSubmit={ChangeSettings}>
            <InputSettings defaultText={userData.firstname} header={"Nom:"} type={"text"} updateField={(value) => updateUserData('firstname', value)} />
            <InputSettings defaultText={userData.name} header={"Prénom:"} type={"text"} updateField={(value) => updateUserData('name', value)} />
            <InputSettings defaultText={userData.username} header={"Username:"} type={"text"} updateField={(value) => updateUserData('username', value)} />
            <InputSettings defaultText={userData.email} header={"email:"} type={"text"} updateField={(value) => updateUserData('email', value)} />
            <button type="submit" className='Submit-button'>Register</button>
          </form>
        ) : (
          <p>Loading user data...</p>
        )}
      </div>
    </div>
  );
};

export default Settings;
